<template>
  <div class="thumb-box">
  <mt-picker :slots="numberSlot"  @change="onValuesChange"></mt-picker>  
  </div>
</template>

<script>

export default {
  data () {
    return {
      numberSlot: [{
        flex: 1,
        defaultIndex: 0,
        values: ['黑', '白', '红'],
        className: 'slot1'
      }],
    }
  },

  methods: {
    onValuesChange(picker, values) {  
      console.log(picker)  
      console.log(values)  
    }  
  },

  computed: {

  },

  created(){
    
  },

  mounted() {
    this.$nextTick(() => {
      this.numberSlot[0].defaultIndex = 2;
    });
  }
}
</script>

<style lang="scss">

</style>


