<template>
  <div class="user-info animated fadeIn">
    <user-head pageTitle="个人资料"></user-head>
    <div class="user-info-list">
      <mt-cell title="头像" is-link>
        <img class="avatar" :src="avatar" alt="">
        <input ref="imgFile" accept="image/gif,image/jpeg,image/jpg,image/png,image/svg" type="file" class="avatar-btn" @change="getImgInfo">
      </mt-cell>
      <mt-cell title="昵称" :value="nickName" is-link></mt-cell>
      <mt-cell title="性别" :value="sexText" is-link @click.native="showSex = true"></mt-cell>
      <mt-cell title="生日" :value="dateValue | dateFormat" is-link @click.native="openPicker"></mt-cell>
      <mt-cell title="现居住地" value="杭州" is-link></mt-cell>
      <mt-cell title="行业" value="" is-link></mt-cell>
    </div>
    <div class="logout-btn">
      <mt-button type="danger" size="large" @click="logout">退出</mt-button>
    </div>
    <mt-datetime-picker
      type="date"
      ref="picker"
      v-model="pickerValue"
      :startDate="new Date('1900-01-01')"
      :endDate="new Date()"
      @confirm="handleConfirm">
    </mt-datetime-picker>
    <mt-popup
      class="img-crop-container"
      v-model="popupVisible">
      <div class="img-box">
        <img ref="imgEle" :src="imgSrc" v-bind:style="{width: avtW + 'px', height: avtH + 'px', left: avtLeft + 'px', top: avtTop + 'px'}" alt="">
      </div>
      <mt-range class="size-bar" v-model="rangeValue"></mt-range>
      <mt-button type="primary" size="large" @click="saveImg">保存</mt-button>
    </mt-popup>
    <mt-popup
      class="popup-tips"
      v-model="tipVisible"
      position="top"
      :modal='false'>
      {{popUpText}}
    </mt-popup>
    <mt-popup
      class="sex-container"
      v-model="showSex"
      position="bottom">
      <mt-picker :slots="sexSlots" @change="onValuesChange" :showToolbar="true" value-key="val">
          <span class="mint-datetime-action mint-datetime-cancel" @click="showSex = false">取消</span>
          <span class="mint-datetime-action mint-datetime-confirm" @click="setSex">确定</span>
      </mt-picker>
    </mt-popup>
    <canvas ref="canvas" id="myCanvas" width="200" height="200">
      Your browser does not support the HTML5 canvas tag.
    </canvas>
  </div>
</template>

<script>

import defaultAvatar from '../assets/avatar.svg';
import UserHead from './UserHead.vue';

export default {
  data () {
    return {
      userInfo: [],
      nickName: '正点财经用户',
      avatar: defaultAvatar,
      rangeValue: 0,
      popupVisible: false,
      pickerValue: new Date('1970-01-01'),
      dateValue: new Date('1970-01-01'),
      imgSrc: '',
      avtW: 200,
      avtH: 200,
      avtLeft: 0,
      avtTop: 0,
      imgWidth: 0,
      imgHeight: 0,
      baseImgWidth: 0,
      baseImgHeight: 0,
      img: new Image(),
      popUpText: '',
      tipVisible: false,
      showSex: false,
      sexSlots: [
        {
          defaultIndex: 0,
          values: [
            {
              index: '0',
              val: '男'
            },
            {
              index: '1',
              val: '女'
            },
            {
              index: '2',
              val: '保密'
            }
          ]
          // values: ['男', '女', '保密']
        }
      ],
      sexObj: {
        index: '2',
        val: '保密'
      },
      sexText: 'nan'
    }
  },

  methods: {
    onValuesChange(picker, values){
      if(values[0]){
        this.sexObj = values[0];
        console.log(this.sexObj);
        if(!this.showSex){
          this.sexText = this.sexObj.val;
        }
      }
    },
    openPicker(){
      this.$refs.picker.open();
    },
    handleConfirm(value){
      this.dateValue = value;
    },
    getImgInfo(){
      var file = this.$refs.imgFile.files[0];
      var reader = new FileReader();
      reader.onload = (e) => {
        this.imgSrc = e.target.result;
        this.rangeValue = 0;
        this.popupVisible = true;

        var img = new Image();
        img.src = this.imgSrc;

        img.onload = () => {

          this.imgWidth = img.width;
          this.imgHeight = img.height;

          console.log(this.imgWidth, this.imgHeight);

          if(this.imgWidth >= this.imgHeight){
            this.avtW = this.imgWidth / this.imgHeight * this.avtH;
          } else {
            this.avtH = this.imgHeight / this.imgWidth * this.avtW;
          }
          
          this.avtLeft = (200 - this.avtW) / 2;
          this.avtTop = (200 - this.avtH) / 2;

          this.baseImgWidth = this.avtW;
          this.baseImgHeight = this.avtH;
        }
      };
      reader.readAsDataURL(file);
    },
    saveImg(){
      this.popupVisible = false;
      var canvas = this.$refs.canvas;
      var context = canvas.getContext("2d");

      context.drawImage(this.$refs.imgEle, 0, 0, this.imgWidth, this.imgHeight, this.avtLeft, this.avtTop, this.avtW, this.avtH);

      var fd = new FormData();

      var blob = this.dataURItoBlob(canvas.toDataURL('image/png'));
      fd.append('picture', blob);

      this.uploadPic("a74703a9-0d6b-45f5-bc11-2ba97c991144", fd)
        .then((res) => {
          console.log(res);
          if(res.data.code = "success"){
            this.showPopup('头像修改成功！');
            this.userInfo[2] = res.data.info;
            this.setCookie(this.userInfo);
          }
        })
        .catch((err) => { console.log(err); });
    },
    dataURItoBlob(dataURI) {
      var byteString = atob(dataURI.split(',')[1]);
      var mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];
      var ab = new ArrayBuffer(byteString.length);
      var ia = new Uint8Array(ab);
      for (var i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
      }
      return new Blob([ab], {type: mimeString});
    },
    showPopup(text, callback){
      this.tipVisible = true;
      this.popUpText = text;
      setTimeout(() => {
        this.tipVisible = false;
        if(callback){
          callback();
        }
      }, 3000);
    },
    logout(){
      this.$cookie.set('zdcjcookie', '', { expires: 0 });
      this.showPopup('登出成功！3秒后跳转到首页。', function(){
        location.href = '#/';
      });
    },
    setSex(){
      this.showSex = false;
      this.sexText = this.sexObj.val;
    }
  },

  watch: {
    rangeValue(val){
      var scale = 1 + val / 100;
      
      this.avtW = this.baseImgWidth * scale;
      this.avtH = this.baseImgHeight * scale;

      this.avtLeft = (200 - this.avtW) / 2;
      this.avtTop = (200 - this.avtH) / 2;
    }
  },

  created(){
    if(!this.checkLogin()){
      location.href = "#/login";
      return ;
    }
    this.userInfo = this.getCookie();
    console.log(this.userInfo);
    this.avatar = this.Api.avatarUrl + this.userInfo[2];
    if(this.userInfo[3]){
      this.nickName = this.userInfo[3];
    }
    if(this.userInfo[10]){
      this.dateValue = this.pickerValue = new Date(this.userInfo[10]);
    }
    this.sex = this.userInfo[11] || '2';
  },

  mounted() {
    this.$nextTick(() => {
      this.sexSlots[0].defaultIndex = Number(this.sex);
    });
  },

  components: {
    UserHead
  }
}
</script>

<style lang="scss">
  @import '../style/mixin';
  .user-info .mint-cell {
    border-bottom: 1px solid #eaeaea;
    .avatar {
      width: 50px;
      margin: 5px 0;
      border-radius: 50%;
    }
  }
  .user-info .avatar-btn {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
  }
  .user-info-list {
    margin-bottom: 30px;
  }
  .logout-btn {
    padding: 0 10px;
  }
  .img-crop-container {
    padding: 20px;
    .img-box {
      position: relative;
      width: 200px;
      height: 200px;
      margin-bottom: 10px;
      overflow: hidden;
      background-color: rgba(0, 0, 0, 0.15);
      img {
        position: absolute;
      }
    }
    .size-bar {
      margin-bottom: 10px;
    }
  }
  .popup-tips {
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: #fff;
    background-color: rgba(0,0,0,.7);
  }
  .sex-container {
    width: 100%;
    .picker-slot {
      width: 100%;
    }
  }
</style>


